<template>
  <div class="flex">
    <div class="flex" @click.stop="radioChange(1)">
        <label>选项一</label>
        <img class="radio" :src="value == 1 ? radioActive : radio" alt="">
    </div>
    <div class="flex" @click.stop="radioChange(2)">
        <label>选项二</label>
        <img class="radio" :src="value == 2 ? radioActive : radio" alt="">
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      radio: require('@/assets/icon/radio.png'),
      radioActive: require('@/assets/icon/radio-active.png'),
      value: '1',
    };
  },
  methods: {
    radioChange(index) {
        this.value = index
    }
  }
};
</script>

<style scoped>
.flex {
    display: flex;
    align-items: center;
}
label {
    margin:0 5px 0 20px;
}

.radio {
    width: 16px;
    height: 16px;
}
</style>
